/* stylelint-enable */
@font-face {
  font-family: "Glober";
  src: url("../fonts/GloberSemiBoldFree.woff2") format("woff2"), url("../fonts/GloberSemiBoldFree.woff") format("woff");
  font-display: swap;
  font-weight: 600;
  font-style: normal; }

html,
body {
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  font-family: "OpenSans", sans-serif;
  font-size: 18px;
  line-height: 28px;
  font-weight: 600;
  color: #545452; }

.body__scroll {
  overflow-y: hidden; }

html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

img {
  width: 100%;
  height: auto;
  border-style: none;
  object-fit: cover;
  font-size: 0;
  display: flex; }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0; }

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

input,
textarea,
select {
  background-color: transparent;
  border: none;
  outline: none; }

button {
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
  color: inherit; }

a {
  display: inline-block;
  color: inherit;
  text-decoration: none;
  background-color: transparent;
  cursor: pointer; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none; }

.container {
  max-width: 1165px;
  padding: 0 15px;
  margin: 0 auto;
  position: relative; }
